.aheto-single-gallery-img{width:100%;background-size:cover;height:400px;cursor:pointer;position:relative}@media (max-width:1440px){.aheto-single-gallery-img{height:325px}}@media (max-width:1200px){.aheto-single-gallery-img{height:250px}}.aheto-single-gallery-img::before{content:'';position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(var(--ca-active),0);-webkit-transition:all .5s;-o-transition:all .5s;transition:all .5s}.aheto-single-gallery-img:hover::before{content:'';background-color:rgba(var(--ca-active),.3);top:0;left:0;width:100%;height:100%;z-index:2;-webkit-transition:all .5s;-o-transition:all .5s;transition:all .5s}.aheto-single-gallery-popup{position:fixed;display:none;top:0;left:0;width:100%;height:100%;background-color:rgba(var(--ca-dark),.6);z-index:9999}.aheto-single-gallery-popup img{max-width:75vw;max-height:75vh;position:relative;top:50%;left:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%)}.aheto-single-gallery-popup .close{position:absolute;top:50px;right:50px;color:var(--c-light);z-index:9999;cursor:pointer}
